import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import CommonTable from "../../pages/table/CommonTable";
import CommonTableColumn from "../../pages/table/CommonTableColumn";
import CommonTableRow from "../../pages/table/CommonTableRow";
import axios from "axios";
import "./Post.css";

const PostList = (props) => {
  const [dataList, setDataList] = useState([]);

  const [showPopup, setShowPopup] = useState(false);

  const togglePopup = (param, event) => {
    if (window.confirm("해당 문의글의 지정 상담사로 선택하시겠습니까?")) {
      axios
        .patch(
          `http://localhost:8000/api/inqueryChoice/${param.inqueryId}`,
          {
            inqueryId: param.inqueryId,
            conselorId: param.counselorId,
          },
          {
            headers: {
              "X-USER-ID": param.counselorId,
            },
          }
        )
        .then((response) => {
          getInquerysList();
        })
        .catch((Error) => {
          console.log(Error);
        });
    } else {
    }
  };

  useEffect(() => {
    getInquerysList();
  }, []);

  const getInquerysList = () => {
    axios
      .get("http://localhost:8000/api/getInquerys")
      .then((response) => {
        setDataList(response.data.response);
      })
      .catch((Error) => {
        console.log(Error);
      });
  };

  return (
    <>
      <div className="title">상담사 문의 목록</div>
      <CommonTable
        headersName={["글번호", "유저ID", "제목", "상담사ID", "등록일"]}
      >
        {dataList
          ? dataList.map((item, index) => {
              return (
                <CommonTableRow key={index}>
                  <CommonTableColumn>{item.id}</CommonTableColumn>
                  <CommonTableColumn>{item.userId}</CommonTableColumn>
                  <CommonTableColumn>
                    <Link
                      to={{
                        pathname: `/PostView`,
                        state: {
                          userId: item.userId,
                          title: item.title,
                          content: item.content,
                          counselorId: item.counselorId,
                          createdAt: item.createdAt,
                          updatedAt: item.updatedAt,
                        },
                      }}
                    >
                      {item.title}
                    </Link>
                  </CommonTableColumn>
                  <CommonTableColumn>
                    {item.counselorId == null ? (
                      <button
                        onClick={() =>
                          togglePopup({
                            inqueryId: item.id,
                            counselorId: item.userId,
                          })
                        }
                      >
                        없음
                      </button>
                    ) : (
                      item.counselorId
                    )}
                  </CommonTableColumn>
                  <CommonTableColumn>{item.createdAt}</CommonTableColumn>
                </CommonTableRow>
              );
            })
          : ""}
      </CommonTable>
    </>
  );
};

export default PostList;
